html {
  height: 100vh;
}

@media (max-width: 560px) {
  html {
    font-size: 10px;
  }
}

@media (min-width: 561px) and (max-width: 710px) {
  html {
    font-size: 10px;
  }
}

@media (min-width: 711px) and (max-width: 914px) {
  html {
    font-size: 12px;
  }
}

@media (min-width: 915px) and (max-width: 1000px) {
  html {
    font-size: 13px;
  }
}

@media (min-width: 1001px) and (max-width: 1130px) {
  html {
    font-size: 14px;
  }
}

@media (min-width: 1131px) {
  html {
    font-size: 15.5px;
  }
}

body {
  margin: 0;
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
